value,\n                parentObjectId: props.parentId,\n                modelBody: [{ key: \"entityDesigner\", model: \"\" }],\n                modelType: \"entityDesigner\",\n                modelAdditionals: {},\n            };\n            const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n            await onAfterModelCreated(createdModel);\n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n\nasync function onAfterModelCreated(createdModel: IModel) {\n    await studio.checkout(createdModel);\n    studio.openItem(createdModel);\n}\n\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\n</script>\n\n<template>\n    <div class=\"nameInputDiv\">\n        <div class=\"input-label\">Name</div>\n        <IDEInput :isRequired=\"true\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" placeholder=\"Name\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emit('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid\" />\n</template>\n\n<style scoped>\n.nameInputDiv {\n    padding-left: 20px;\n    padding-right: 20px;\n    padding-top: 18px;\n}\n\n.input-label {\n    color: #000000;\n    font-weight: 500;\n    font-family: 'Roboto';\n    font-size: 14px;\n    margin-left: 6px;\n}\n</style>\n"],"sourceRoot":""}]);
